"use client";
import React from "react";
import Navbar from "../../src/components/Navbar";
import styles from "../styles/Card.module.css";

export default function Yazilar() {
  return (
    <>
      <Navbar />
      <main style={{ maxWidth: 700, margin: '0 auto', padding: '2rem 1rem' }}>
        <h1 style={{ fontSize: '2rem', marginBottom: '1.5rem' }}>Yazılar</h1>
        
        <div className={styles.grid}>
          {/* Örnek yazı kutuları */}
          <div className={styles.card}>
            <h2 className={styles.cardTitle}>React ile Modern Web Geliştirme</h2>
            <p className={styles.cardDescription}>
              Modern web uygulamaları geliştirmek için React kullanımı ve best practices...
            </p>
            <p className={styles.cardLink}>
              Detaylar için tıklayınız {'>>'}
            </p>
          </div>

          <div className={styles.card}>
            <h2 className={styles.cardTitle}>TypeScript ile Tip Güvenliği</h2>
            <p className={styles.cardDescription}>
              TypeScript kullanarak daha güvenli ve sürdürülebilir kod yazmanın yolları...
            </p>
            <p className={styles.cardLink}>
              Detaylar için tıklayınız {'>>'}
            </p>
          </div>

          <div className={styles.card}>
            <h2 className={styles.cardTitle}>Next.js ile SEO Optimizasyonu</h2>
            <p className={styles.cardDescription}>
              Next.js kullanarak web sitenizin SEO performansını nasıl artırabilirsiniz...
            </p>
            <p className={styles.cardLink}>
              Detaylar için tıklayınız {'>>'}
            </p>
          </div>
        </div>
      </main>
    </>
  );
} 